import EventEmiter from 'events'

class Dialog extends EventEmiter {
  /**
   * 
   * @param {string} name 
   * @param  {...any} opts 
   */
  open (name, ...opts) {
    this.emit('open', name, ...opts)
  }

  /**
   * 
   */
  close () {
    this.emit('close')
  }

  /**
   * 
   * @param {string} name 
   * @param  {...any} opts 
   */
  openState (name, ...opts) {
    this.emit('openState', name, ...opts)
  }

  /**
   * 
   * @param {string} color 
   */
  setBgColor (color) {
    this.emit('setBgColor', color)
  }
}

export default new Dialog()
